

























export default {
	data() {
		return {
			mobile: '',
			verifyCode: '',
			timer: 20,
			show: false,
			title: '获取验证码'
		};
	},
	onLoad() {
		uni.setNavigationBarTitle({
			title: '注册'
		});
	},
	methods: {
		getVerifyCode: function() {
			var _this = this;
			uni.request({
				url: this.apiServer + '/user/verify',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					mobile: _this.mobile
				},
				success: res => {
					if (res.data.code === 0) {
						uni.showToast({
							title: '验证码已发送'
						});
						_this.disabled = true;
						console.log(_this.disabled);
						let timer1 = setInterval(() => {
							_this.show = true;
							_this.timer--;
							_this.title = _this.timer + 's后再试';
							if (_this.timer == 0) {
								clearInterval(timer1);
								_this.timer = 20;
								_this.show = false;
								_this.title = '发送验证码';
								return;
							}
						}, 1000);
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
				}
			});
		},
		checkCode: function() {
			var _this = this;
			console.log(_this.verifyCode);
			uni.request({
				url: this.apiServer + '/user/check',
				method: 'POST',
				header: { 'content-type': 'application/x-www-form-urlencoded' },
				data: {
					mobile: _this.mobile,
					verifyCode: _this.verifyCode
				},
				success: res => {
					console.log(res.data);
					if (res.data.code === 0) {
						uni.navigateTo({
							url: '../password/password?mobile=' + _this.mobile
						});
					} else {
						uni.showModal({
							title: '提示',
							content: res.data.msg
						});
					}
				}
			});
		}
	}
};
